import React, { useState } from "react"
import "./index.css"
import $ from "jquery"
export default function UserRegister (props){
    const[username,setusername]=useState("");
    const[nickname,setnickname]=useState("");
    const[password,setpassword]=useState("");
    const[student_id,setstudent_id]=useState("")
    const[xb,setxb]=useState("")
    const register=()=>{
        $.ajax({
            type:"post",
            url:"http://127.0.0.1:8081/user/register",
            data:{"username":username,"nickname":nickname,"password":password,"student_id":student_id,"six":xb},
            success:function(res){
                console.log(res);
                if(res.status=="200"&&res.data=="1"){
                    alert(" ~");
                    sessionStorage.setItem("username",username);
                    sessionStorage.setItem("password",password);
                    setInterval(function(){
                        props.history.push("/userlogin")
                    },1000);
                }else if(res.status=="200"&&res.data=="2"){
                    alert("");
                }else if(res.status=="200"&&res.data=="0"){
                    alert(" ");
                }
            },
            error:function(err){
                console.log("  500");
                console.log(err);
            }
            
        });
    }
    return(
        <div>
            <div className="register_box">
                <div className="title">
                    <h2></h2>
                </div>
                <div className="xxx">
                    <span id="unStatus">
                            
                        </span>
                    <span id="pwStatus">
                            
                        </span>
                    <div className="un">
                        <input type="text" placeholder="" id="username" value={username} onChange={(e)=>{setusername(e.target.value)}}/>
                    </div>
                    <div className="un">
                        <input type="text" placeholder="" id="name" value={nickname} onChange={(e)=>{setnickname(e.target.value)}}/>
                    </div>
                    <div className="pw">
                        <input type="password" placeholder="" id="password" value={password} onChange={(e)=>{setpassword(e.target.value)}}/>
                    </div>
                    <div className="xh">
                        <input type="text" placeholder="" id="xh" value={student_id} onChange={(e)=>{setstudent_id(e.target.value)}}/>
                    </div>
                    <div className="xb">
                        <input type="radio" id="main" value="" name="sex" onChange={(e)=>{setxb(e.target.value)}} defaultChecked={xb===""?true:false}/><label htmlFor="main"></label>
                        <input type="radio" id="wuman" value="" name="sex" onChange={(e)=>{setxb(e.target.value)}} defaultChecked={xb===""?true:false}/><label htmlFor="wuman"></label>
                    </div>
                    <div className="b">
                        <input type="button" value="" id="btn" onClick={register}/>
                    </div>
                </div>
            </div>
        </div>
    )
}