import React, { useContext,useEffect,useState } from "react"
import {C} from "../../../main"
import $ from "jquery"
export default function ModfilyStudent(){
    const {success_state,setsuccess_state} = useContext(C);

    const[username,setusername]=useState("");
    const[nickname,setnickname]=useState("");
    const[password,setpassword]=useState("");
    const[student_id,setstudent_id]=useState("");
    const[studentsix,setstudentsix]=useState("");
    const[timeout,settimeout]=useState("");
    const[select_state,setselect_state]=useState(false);
    useEffect(()=>{
        setsuccess_state(false);
    },[])
    const username_change_fun=(val)=>{
        setusername(val);
        console.log(val);
        clearTimeout(timeout);
        settimeout(setTimeout(function(){
        	$.ajax({
                type:"get",
                url:`http://127.0.0.1:8081/user/findByUsername?username=${val}`,
                async:false,
                success:function(res){
                    if (res.status===200) {
                        console.log("");
                        console.log(res.data);
                        setusername(res.data.username);
                        setnickname(res.data.nickname);
                        setpassword(res.data.password);
                        setstudent_id(res.data.student_id);
                        setstudentsix(res.data.six);
                        setsuccess_state(false);
                        // 
                        setselect_state(true);
                    }else{
                        console.log("");
                        setnickname("");
                        setpassword("");
                        setstudent_id("");
                        setstudentsix("");
                        setsuccess_state(false);
                        setselect_state(false);
                    }
                }
            })
    	}, 1000))

    }
    const Confirm_modification=()=>{
        if (select_state) {
            $.ajax({
                type:"get",
                url:`http://127.0.0.1:8081/user/updateUser?username=${username}&nickname=${nickname}&password=${password}&student_id=${student_id}&six=${studentsix}`,
                async:false,
                success:function(res){
                    console.log(res);
                    if (res.status===200&&res.data==="1") {
                        console.log(res.data);
                        setusername("");
                        setnickname("");
                        setpassword("");
                        setstudent_id("");
                        setstudentsix("");
                        setsuccess_state(true);
                        // 
                        setselect_state(false);
                    }else{
                        setnickname("");
                        setpassword("");
                        setstudent_id("");
                        setstudentsix("");
                        setsuccess_state(false);
                        setselect_state(false);
                    }
                }
            })
        }else{
            alert("");
        }
    }
    return(
        <div className="addstudent_body_content">
            <div className="title_sutdent"></div>
            <div className="addstudent_context">
                <span style={{color:"red",fontSize:"20px"}}>*:</span>
                <div className="addstudent_context_item">
                    <div className="addstudent_context_item_title">
                        <label htmlFor="xszh"><b>:</b></label>
                    </div>
                    <input type="text" value={username} onChange={(e)=>{username_change_fun(e.target.value)}} id="xszh" className="form-control" placeholder=""/>
                    {username===""?(<span></span>):""}
                </div>
                <div className="addstudent_context_item">
                    <div className="addstudent_context_item_title">
                        <label htmlFor="xsxm"><b>:</b></label>
                    </div>
                    <input type="text" value={nickname} onChange={(e)=>{setnickname(e.target.value)}} id="xsxm" className="form-control" placeholder=""/>
                    {nickname===""?(<span></span>):""}
                </div>
                <div className="addstudent_context_item">
                    <div className="addstudent_context_item_title">
                        <label htmlFor="xsmm"><b>:</b></label>
                    </div>
                    <input type="text" value={password} onChange={(e)=>{setpassword(e.target.value)}} id="xsmm" className="form-control" placeholder=""/>
                    {password===""?(<span></span>):""}
                </div>
                <div className="addstudent_context_item">
                    <div className="addstudent_context_item_title">
                        <label htmlFor="xsxh"><b>:</b></label>
                    </div>
                    <input type="text" value={student_id} onChange={(e)=>{setstudent_id(e.target.value)}} id="xsxh" className="form-control" placeholder=""/>
                    {student_id===""?(<span></span>):""}
                </div>
                <div className="addstudent_context_item">
                    <div className="addstudent_context_item_title">
                        <label htmlFor="xsbh"><b>:</b></label>
                    </div>
                    <input type="radio" id="man" value="" name="six" style={{width:"15px"}} onChange={(e)=>{setstudentsix(e.target.value)}} checked={studentsix===""?true:false}/>
                    <label htmlFor="man" style={{float:"left",marginLeft:"10px",lineHeight:"30px"}}></label>
                    <input type="radio" id="wuman" value="" name="six" style={{width:"15px",marginLeft:"10px"}} onChange={(e)=>{setstudentsix(e.target.value)}} checked={studentsix===""?true:false}/>
                    <label htmlFor="wuman" style={{float:"left",marginLeft:"10px",lineHeight:"30px"}}></label>
                    {/* {studentsix===""?(<span style={{marginLeft:"138px"}}></span>):""} */}
                </div>
                <button type="button" 
                        className="btn btn-warning" 
                        style={{marginLeft:"130px",marginTop:"20px"}} 
                        data-bs-toggle="modal" 
                        data-bs-target="#revise">
                        
                </button>

                {/* <!--  --> */}
                <div className="modal" id="revise">
                    <div className="modal-dialog">
                        <div className="modal-content">

                        {/* <!--  --> */}
                        <div className="modal-header">
                            <h4 className="modal-title"></h4>
                            <button type="button" className="btn-close" data-bs-dismiss="modal"></button>
                        </div>

                        {/* <!--  --> */}
                        <div className="modal-body">
                            <ul>
                                <li>:<b>{student_id}</b></li>
                                <li>:<b>{nickname}</b></li>
                                <li>:<b>{studentsix}</b></li>
                                <li>:<b>{username}</b></li>
                                <li>:<b>{password}</b></li>
                            
                            </ul>
                        </div>

                        {/* <!--  --> */}
                        <div className="modal-footer">
                            <button type="button" className="btn btn-primary" data-bs-dismiss="modal" ></button>
                            <button type="button" className="btn btn-danger" data-bs-dismiss="modal" onClick={Confirm_modification}></button>                            
                        </div>

                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}