import React, { useEffect, useState } from "react"
import "./index.css"
import $ from "jquery"
export default function UsserLogin(props){

	const[un,setun]=useState(sessionStorage.getItem("username"));
	const[pwd,setpwd]=useState(sessionStorage.getItem("password"));

    const goRegister=()=>{
        props.history.push("/userregister")
    }
	
	useEffect(()=>{
		if (un!==""&&pwd!=="") {
			sessionStorage.setItem("username","");
			sessionStorage.setItem("password","");
		}
	},[])

	const login=()=>{
		$.ajax({
			type:"post",
			url:"http://127.0.0.1:8081/user/login",
			data:{"username":un,"password":pwd},
			success:function(res){
				console.log(res);
				if(res.status=="200"&&res.data=="1"){
					console.log("~");
					alert("");
				}else if(res.status=="200"&&res.data=="0"){
					console.log("");
					alert("    ");
				}
			},
			error:function(err){
				console.log("");
				console.log(err);
			}
		});
	}
    return(
        <div>
            <div className="box">
			<div className="title">
				<h2></h2>
			</div>
			<div className="xxx">
					<div className="un">
						<input type="text" placeholder="" id="username" value={un} onChange={(e)=>{setun(e.target.value)}} />
					</div>
					<div className="pw">
						<input type="password" placeholder="" id="password" value={pwd} onChange={(e)=>{setpwd(e.target.value)}}/>
					</div>
					<div className="b">
						<input type="button" value="" id="btn" onClick={login}/>
					</div>
			</div>
			<div className="zaxiang">
				<span>?</span>
				<a href="#" onClick={goRegister}></a>
				<a href=""></a>
			</div>
		    </div>
        </div>
    )
}